<template>
  <div id="search">
    <v-container>
      <v-icon @click="$router.push('/')">mdi-arrow-left</v-icon>
      <v-row>
        <v-col v-for="list in searchQuery()" :key="list.id" md="2" sm="3" cols="6">
          <appCards :list="list" :image="list.image.medium" size="100%" />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import { showFilter } from "@/filters/filter-by-shows.js";
import appCards from "@/components/card-for-shows/Cards.vue";
import { mapGetters } from "vuex";
import getAllShows from "@/mixins/fetch-shows.js";

export default {
  name: "appSearch",
  components: {
    appCards,
  },
  mixins: [getAllShows],
  data() {
    return {
      showId: this.$route.params.id,
    };
  },
  computed: {
    ...mapGetters({ showList: "allShowsList" }),
  },
  methods: {
    searchQuery() {
      let searchResult = {};
      this.showId = this.$route.params.id;
      if (!this.showList.length) {
        this.getAllShows();
      }
      searchResult = showFilter(this.showList, this.showId);
      return searchResult;
    },
  },
};
</script>
<style scoped>
.detailed-card {
  margin-left: 10px;
  margin-top: 20px;
}
</style>
