<template>
  <div id="Cards">
    <v-hover v-slot:default="{ hover }">
      <v-card
       :width="size"
        :elevation="hover ? 24 : 1"
        :outlined="hover"
        :light="hover"
        @click="moreDetails(list.id)"
        class="ma-1"
      >
        <v-img :src="image" class="align-end">
          <v-card-title v-if="hover" class="align-top"
            ><span class="grey darken-2 white--text  body-2">{{
              list.name.toUpperCase()
            }}</span></v-card-title
          >
          <v-card-text v-if="hover">
            <span
              v-for="genre in list.genres"
              :key="genre"
              class="grey darken-2 white--text  overline"
            >
              •{{ genre }} <br /></span

            ><v-rating
              v-model="rating"
              readonly
              color="yellow"
              background-color="white darken-2"
              small
              half-increments
            ></v-rating
            ><span class="grey darken-2 white--text"
              >RATING: {{ rating }}/5</span
            ></v-card-text
          >
        </v-img>
      </v-card>
    </v-hover>
  </div>
</template>
<script>
export default {
  name: "appCards",
  props: {
    list: {
      required: true,
    },
    image:{
    },
    size:{}
  },
  computed: {
    rating() {
      return this.list.rating.average / 2;
    },
  },
  methods: {
    moreDetails(showId) {
      this.$router.push({ name: "showDetails", params: { id: showId } });
    },
    
  },
};
</script>
<style scoped></style>
