import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allShowsList: {},
  },
  mutations: {
    setAllShowsList: (state, list) => {
      state.allShowsList = list;
    },
  },
  actions: {},
  modules: {},
  getters: {
    allShowsList: (state) => state.allShowsList,
  },
});
