<template>
  <div id="Shows">
    <topShows v-if="showsList.length" :showsList="showsList" />
    <div v-for="(genre, key) in genres" :key="key">
      <v-hover v-slot:default="{ hover }">
        <div class="mx-12 mt-12 pl-6 pointer"  @click="showSelectedGenre(genre)">
          <span class="font-weight-bold white--text">
            {{ genre
            }}
          </span>
          <v-btn
            v-if="hover"
            class="overline no-background-hover"
            :text="true"
            :tile="false"
            :ripple="false"
            small
          >
            Explore All
            <v-icon color="white">mdi-chevron-right</v-icon>
          </v-btn>
        </div>
      </v-hover>
      <v-slide-group class="px-auto" show-arrows>
        <v-slide-item v-for="(list, key) in genreFilter(showsList, genre).slice(0, 12)" :key="key">
          <appCards :list="list" :image="list.image.medium" size="200" />
        </v-slide-item>
      </v-slide-group>
    </div>
  </div>
</template>
<script>
import { listOfAllShows } from "../../service/index.js";
import { genreFilter } from "@/filters/filter-by-genre.js";
import appCards from "@/components/card-for-shows/Cards.vue";
import topShows from "@/components/top-shows/TopShows.vue";
import {mapGetters} from "vuex";
import getAllShows from "@/mixins/fetch-shows.js";

export default {
  name: "appShows",
  data() {
    return {
      //showsList: [],
      genres: [
        "Action",
        "Horror",
        "Comedy",
        "Drama",
        "Romance",
        "Adventure",
        "Thriller",
        "Science-Fiction",
      ],
    };
  },
  components: {
    appCards,
    topShows
  },
  mixins:[getAllShows],
  created() {
    this.getAllShows();
  },
  methods: {
    genreFilter(list, genre) {
      return genreFilter(list, genre);
    },
    showSelectedGenre(genre) {
      this.$router.push({ name: "showByGenre", params: { id: genre } });
    },
    moreDetails(showId) {
      this.$router.push({ name: "showDetails", params: { id: showId } });
    }
  },
  computed:{
    ...mapGetters({showsList:'allShowsList'})
  }
};
</script>

<style scoped>
.normal {
  width: 400px;
  height: 200px;
}
.no-background-hover::before {
  background-color: transparent;
}
.pointer {
  cursor: pointer;
}
</style>
