<template>
  <div>
    <v-slide-group class="pa-4" show-arrows>
      <v-row>
        <v-col v-for="(list, key) in details" :key="key">
          <v-card class="mx-auto" width="300">
            <v-img :src="image(list.image)" class="align-end">
              <v-card-title v-if="!list.image">Image not found</v-card-title>
            </v-img>
            <v-container style="max-height: 150px;" class="overflow-y-auto">
              <v-card-title v-if="!list.summary">No Summary found</v-card-title>
              <v-card-text v-html="list.summary"></v-card-text>
            </v-container>
          </v-card>
        </v-col>
      </v-row>
    </v-slide-group>
  </div>
</template>

<script>
export default {
  name: "Episodes",
  props: {
    details: {
      type: Array,
    },
  },
  methods: {
    image(value) {
      if (value ?? false) {
        return value.medium;
      }
    },
  },
};
</script>
