<template>
  <div v-if="showDetail.id" id="showDetails">
    <v-icon @click="$router.go(-1)" class="ml-4 ma-1 mt-3"
      >mdi-arrow-left</v-icon
    >
    <v-icon @click="$router.push('/')" class="ml-4 ma-1 mt-3">mdi-home</v-icon>
    <v-row>
      <v-col class="ml-md-12 ml-sm-8" md="4" sm="5" cols="11">
        <v-hover v-slot:default="{ hover }">
          <v-card
            class="mt-8 ml-5"
            width="100%"
            :elevation="hover ? 24 : 1"
            :outlined="hover"
            :light="hover"
          >
            <v-img :src="showDetail.image.original"></v-img>
          </v-card>
        </v-hover>
      </v-col>
      <v-col class="ml-md-2" justify="center" >
        <Summary :showDetail="showDetail" />
      </v-col>
    </v-row>

    <div class="headline ml-12 mt-4 mb-2" @click="showEpisodes = !showEpisodes">
      Episodes
      <v-icon>{{
        showEpisodes ? "mdi-chevron-up" : "mdi-chevron-down"
      }}</v-icon>
    </div>

    <div v-if="showDetail._embedded.episodes.length && showEpisodes">
      <v-row class="ml-12">
        <v-col cols="2">
          <v-select
            :items="getSeasons"
            placeholder="Season 1"
            v-model="season"
          ></v-select>
        </v-col>
      </v-row>

      <EpisodeDetail :details="getEpisodes" />
    </div>

    <div class="headline ml-12 mt-4 mb-2" @click="showCast = !showCast">
      Cast
      <v-icon>{{ showCast ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
    </div>
    <Team v-if="showCast" :details="showDetail._embedded.cast" />

    <div class="headline ml-12 mt-4 mb-2" @click="showCrew = !showCrew">
      Crew
      <v-icon>{{ showCrew ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
    </div>

    <Team v-if="showCrew" :details="showDetail._embedded.crew" />
  </div>
</template>
<script>
import { showDetails, showEpisodes } from "../../service/index.js";
import Summary from "@/components/show-summary/Summary.vue";
import Team from "@/components/cast-and-crew/Team.vue";
import EpisodeDetail from "@/components/episodes/EpisodeDetails.vue";

export default {
  name: "showDetails",
  components: {
    Summary,
    Team,
    EpisodeDetail
  },
  data() {
    return {
      showId: this.$route.params.id,
      showDetail: {},
      showCast: false,
      showCrew: false,
      showEpisodes: false,
      season: "season 1"
    };
  },
  created() {
    this.getShowDetails();
  },
  methods: {
    getShowDetails() {
      const details = showDetails(this.showId);
      details
        .then(response => {
          this.showDetail = response.data;
        })
        .catch(error =>{
          this.$router.push({name:"networkError", params:{message:error}});
        })
    },
    image(value) {
      if (value ?? false) {
        return value.medium;
      }
    }
  },
  computed: {
    getSeasons() {
      let noOfSeason = this.showDetail._embedded.episodes[
        this.showDetail._embedded.episodes.length - 1
      ].season;
      let seasonList = [];
      for (let iterator = 0; iterator < noOfSeason; iterator++) {
        seasonList.push(`Season ${iterator + 1}`);
      }
      return seasonList;
    },
    getEpisodes() {
      let episodes = [];
      this.showDetail._embedded.episodes.forEach(element => {
        if (element.season === Number(this.season.substring(7, 8))) {
          episodes.push(element);
        }
      });
     
      return episodes;
    }
  }
};
</script>
<style scoped>
.contain {
  display: flex;
}
</style>
