<template>
<div>
      <v-slide-group
        class="pa-4"
        show-arrows
      >
        <v-slide-item
          v-for="(list, key) in details"
          :key="key"
        >
          <v-card class="ma-1" width="200">
            
            <v-img
              :src="image(list.person.image)"
              class="align-end"
            >
             <v-card-title v-if="!list.person.image">Image not found</v-card-title>
              <v-card-title class="title">{{ list.person.name }}</v-card-title>
              <v-card-subtitle v-if="list.type">{{ list.type }}</v-card-subtitle>
            </v-img>
          </v-card>
        </v-slide-item>
      </v-slide-group>
     
   
    </div>
</template>

<script>
export default {
    name:'Team',
    props:{
        details:{
            type:Array
        }
    },
    methods:{
         image(value){
      if(value ?? false)
      {
        return value.medium;
      }
      
    }
    }
    
}
</script>