<template>
  <div id="header">
    <v-toolbar dense flat>
      <v-toolbar-title
        ><router-link to="/" class="none white--text">
          TV Shows</router-link
        ></v-toolbar-title
      >
      <v-spacer></v-spacer>
      <div class="padding">
        <v-text-field
          @input="searchQueryChanged"
          v-if="showSearchBar"
          v-model="searchQuery"
          rounded
          color="white"
          outlined
          dense
          label="Search Shows"
        ></v-text-field>
      </div>
      <v-btn icon>
        <v-icon @click="showSearchBar = !showSearchBar"> mdi-magnify</v-icon>
      </v-btn>
    </v-toolbar>
  </div>
</template>

<script>
export default {
  name: "appHeader",
  data() {
    return {
      showSearchBar: false,
      searchQuery: "",
    };
  },
  methods: {
    searchQueryChanged() {
      if (this.searchQuery.length) {
        this.$router.push({ name: "search", params: { id: this.searchQuery } });
      } else {
        this.$router.push("/");
      }
    },
  },
};
</script>

<style scoped>
.padding {
  margin-top: 30px;
  margin-right: 20px;
  size: 50px;
}
.none {
  text-decoration: none;
}
</style>
