<template>
  <div id="genre">
    <v-container>
    <div class="white--text headline mx-5 mt-5">
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-icon @click="$router.go(-1)" v-on="on">mdi-arrow-left</v-icon>
          {{ genre }}
        </template>
        <span>Back</span>
      </v-tooltip>
    </div>
    <v-row>
      <v-col v-for="list in filteredGenre" :key="list.id" cols="auto">
        <v-container class="detailed-card">
          <appCards :list="list" :image="list.image.medium" size="200" />
        </v-container>
      </v-col>
    </v-row>
    </v-container>
  </div>
</template>
<script>
import { genreFilter } from "@/filters/filter-by-genre.js";
import appCards from "@/components/card-for-shows/Cards.vue";
import getAllShows from "@/mixins/fetch-shows.js";
import {mapGetters} from "vuex";
export default {
  name: "showByGenre",
  data() {
    return {
      genre: this.$route.params.id,
      filteredGenre: {}
    };
  },
  mixins:[getAllShows],
  components: {
    appCards
  },
  async created() {
    console.log(this.showsList.length)
    if(!this.showsList.length)
    {
     await this.getAllShows();
    }
    this.filteredGenre = this.filterByGenre(this.showsList, this.genre)
  },
  computed: {
    ...mapGetters({showsList:'allShowsList'})
  },
  methods: {
    filterByGenre(list, genre) {
      return genreFilter(list, genre);
    }
  }
};
</script>
<style scoped>
.detailed-card {
  margin-left: 20px;
  margin-top: 20px;
}
</style>
