export const showFilter = (list,query) =>{
    
    let finalList = [];
  //  console.log("this is filter",list)
    for (let iterator in list) {
       // console.log(list[iterator].name.toLowerCase())
     if(list[iterator].name.toLowerCase().indexOf(query.toLowerCase())!==-1)
      {
          finalList.push(list[iterator]);
      }
      else{
        continue;
      }
    }
   // console.log("another filter",finalList[0])
return finalList;
}