<template>
  <div>
    <div class="hidden-xs-only">
     <v-container align="center" justify="center" class="pb-sm-0 mb-sm-0">
    <v-row class="ml-md-9"> 
      <v-col class="mx-md-9" md="6" sm="7">
        <v-sheet class="mt-sm-0 ml-sm-10 pl-sm-5 pt-sm-0">
          <v-row > 
            <v-col  v-for="(list, i) in topShowsList" :key="i" md="4" sm="4" class="pa-md-3">
              <v-hover v-slot:default="{ hover }">
                <v-card
               width="90%"
                  :elevation="hover ? 24 : 1"
                  :outlined="hover"
                  :light="hover"
                  @click="selected(list,i)"
                >
                  <v-img :src="list.image.medium"></v-img>
                </v-card>
              </v-hover>
            </v-col>
          </v-row>
        </v-sheet>
        <v-col class="body-1" sm="11" >
          <v-row align="center" justify="center" > 
            {{`Top Shows`.toUpperCase()}}
          </v-row>
        </v-col>
      </v-col>
      <v-col md="4" sm="4" class="ml-md-12 ml-sm-12 pb-sm-0 mb-sm-0 pt-md-0 pt-sm-0" align="center" justify="center" >
          <v-col >
            <appCards :list="selectedShow" :image="selectedShow.image.original" size="100%"/>
          </v-col>
      </v-col>
    </v-row>
    </v-container>
      <v-container class="mt-sm-0 pt-sm-0" >
        <v-col>
          <Summary :showDetail="selectedShow" />
        </v-col>
      </v-container>
  </div>
  <div class="hidden-sm-and-up"  align="center" justify="center" >
    <v-row >
      <v-col cols="12" class="mb-0 pb-0" > 
      <v-carousel
    cycle
    hide-delimiters
    show-arrows
  >
    <v-carousel-item
      v-for="(list, i) in topShowsList" :key="i"
    >
     <appCards :list="list" :image="list.image.original" size="80%"/>
    </v-carousel-item>
      </v-carousel>
      </v-col>
    </v-row>
  </div>
  </div>
</template>

<script>
import Summary from "@/components/show-summary/Summary.vue";
import appCards from "@/components/card-for-shows/Cards.vue";
export default {
  name: "topShows",
  props: {
    showsList: {
      type: Array
    }
  },
  data() {
    return {
      topShowsList: [],
      selectedShow: {},
     
    };
  },
  components: {
    appCards,
    Summary
  },
  created() {
    this.topShowsList = this.getTopShows;
    this.counter=0;
  },
  computed: {
    getTopShows() {
      let localList = [...this.showsList];
      localList.sort((first, second) => {
        return second.rating.average - first.rating.average;
      });
      this.selectedShow = localList[0];
      return localList.slice(1, 7);
    }
  },
  methods: {
    selected(show, index) {
      this.topShowsList[index] = this.selectedShow;
      this.selectedShow = show;
  
     // this.getTopShows;
    },
  }
};
</script>

<style scoped>
.align{
  align-content: center;
  justify-content: center;
}
</style>