import  { listOfAllShows } from "@/service/index.js";
const getAllShows ={
    methods: {
        async getAllShows() {
            try{
          const showsListPromise = await listOfAllShows();
          this.$store.commit("setAllShowsList", showsListPromise.data);
          }catch(error){
           
            this.$router.push({name:"networkError", params:{message:error}});
          }
        }
    }   
}
export default getAllShows;