export const genreFilter = (list,genre) => {
    let finalList = [];
    let genreSize = 0;
    for (let iterator in list) {
      genreSize = list[iterator].genres.length;

      for (let genreIterator in list[iterator].genres) {
        if (list[iterator].genres[genreIterator] == genre)
          finalList.push(list[iterator]);
      }
    }
    return finalList;
}
//export default genreFilter